export default {
  /**
   * 
   * @param {Date} Date
   * @returns {String} ,  2016-04-01
   */
  dateToString (dateObj) {
    let y = dateObj.getFullYear()
    let m = dateObj.getMonth()+1
    let d = dateObj.getDate()
    
    if (m<10) {
      m = '0' + m
    }
    if (d<10) {
      d = '0' + d
    }

    return `${y}-${m}-${d}`
  },
}
