import axios from 'axios'
import Config from '../../config'

// 1.postget
// 2.url
// 3.localstoragetokenheader
// 4.
// 5.401


let Axios = {
  _createUrl: function(url){
    const apiUrl = Config.apiUrl[Config.env]
    let reqUrl = apiUrl+url

    return reqUrl
  },
  _createHeaders: function(){
    let token = localStorage.getItem("token") || '' 
    let headers = {
      'X-Auth-Token': token,
      'Content-Type': 'application/json'
    }

    return headers
  },
  get: function(url, params){
    let reqUrl = this._createUrl(url)
    let options = {
      headers: this._createHeaders(),
      params: params
    }

    let Request = axios.get(reqUrl, options).then((response)=>{
                      return response.data
                    })
                    .catch((error)=>{
                      if (error.response && error.response.status == '401'){
                        window.location = '/login'
                      }
                    })

    return Request
  },
  post: function(url, params){
    let reqUrl = this._createUrl(url)
    let options = {
      headers: this._createHeaders()
    }

    let Request = axios.post(reqUrl, params, options).then((response)=>{
                      return response.data
                    })
                    .catch((error)=>{
                      if (error.response && error.response.status == '401'){
                        window.location = '/login'
                      }
                    })

    return Request
  },
}

export default Axios